// SECTION dashboard :: START

.dashboard {
	overflow: unset;
	padding-top: 100px;

	.main__bg {
		z-index: -1;
	}

	&__option {
		margin-bottom: 20px;
		border-radius: 6px;
		box-shadow: 0 1px 20px 0 rgba(17, 30, 79, 0.1);
		background-color: #ffffff;

		&-exposure,
		&-margin,
		&-pl {
			@include adaptive(374) {
				width: 100%;
				max-width: 50%;
				padding-bottom: 0.5rem;
			}

			p {
				font-size: 12px;
				font-weight: 600;
				letter-spacing: 0.47px;
			}

			span {
				font-size: 18px;
				letter-spacing: 0.47px;
			}
		}

		&-exposure {
			@include adaptive(374) {
				padding-left: 0 !important;
				padding-bottom: 0 !important;
			}

			span {
				color: #22c993;
			}
		}

		&-margin {
			@include adaptive(374) {
				text-align: right;
			}
		}

		&-margin,
		&-pl {
			span {
				color: #f05858;
			}
		}

		.dropdown {
			width: 100%;
			@include adaptive(374) {
				max-width: 100% !important;

				&:nth-of-type(2) {
					padding-top: 0.5rem;
				}
			}

			&--border {
				max-width: 167px;

				&:nth-of-type(3) {
					@include adaptive(991) {
						margin-left: calc(140px + 1.5rem);
					}
					@include adaptive(767) {
						margin-left: auto;
					}
				}

				.dropdown-toggle {
					background-color: #ffffff;
					border: 1px solid rgba(185, 185, 185, 0.3);
				}
			}

			&--bg {
				max-width: 140px;

				.dropdown-toggle {
					background-color: #6772e5;
					color: #ffffff;
					font-weight: bold;

					&:hover {
						color: #fff;
					}
				}

				&.show {
					.btn {
						color: #fff;
						@include transition(color);
					}
				}
			}

			&-toggle {
				width: 100%;
			}
		}
	}

	&__block {
		margin-bottom: 20px;
		border-radius: 6px;
		box-shadow: 0 1px 20px 0 rgba(17, 30, 79, 0.1);
		background-color: #ffffff;

		.table {
			font-size: 12px;
			letter-spacing: 0.23px;

			thead {
				th {
					border-bottom: none;
				}
			}

			th,
			td {
				border-color: #D8D8D8;
			}

			th,
			strong {
				font-weight: bold;
			}

			th {
				@include adaptive(767) {
					white-space: nowrap;
				}
			}
		}

		&-notification {
			.table {
				border-bottom: 1px solid #D8D8D8;

				p {
					font-weight: 500;

					span {
						white-space: nowrap;
					}
				}
			}
		}

		.dropdown {
			&--border {
				.dropdown-toggle {
					min-width: 128px;
					background-color: #ffffff;
					border: 1px solid rgba(185, 185, 185, 0.3);

					&:after {
						display: block;
					}
				}
			}

			&-toggle {
				@include transition(color);

				&:focus,
				&.focus {
					outline: none;
					box-shadow: none;
				}

				&.is-active {
					color: #fff;
					@include transition(color);
				}
			}
		}

		&-header {
			min-height: 60px;
			padding-right: 55px;

			.dropdown {
				&-toggle {
					.icon-font {
						color: #AEAEAE;
						font-size: 5px;
					}

					&:after {
						display: none;
					}
				}
			}
		}

		&-close {
			overflow: hidden;
			z-index: 10;
			position: absolute;
			top: 0;
			right: 0;
			width: 30px;
			height: 30px;
			border-top-right-radius: 6px;
			border-bottom-left-radius: 6px;
			background-color: #b3c5dc;

			.icon-font {
				color: #ffffff;
				font-size: 10px;
			}
		}

		&-title {
			font-size: 18px;
			font-weight: 600;
			letter-spacing: 0.42px;
		}

		&-btn {
			height: 40px;
			padding-left: 20px;
			padding-right: 20px;
			border-radius: 6px;
			border: 1px solid #6772e5;
			background-color: #6772e5;
			color: #ffffff;
			font-size: 14px;
			font-weight: 600;
			letter-spacing: 0.44px;
			text-align: center;
			@include transition(background-color, color);

			&--border {
				background-color: transparent;
				color: #6772e5;

				&:hover {
					background-color: #6772e5 !important;
					color: #ffffff;
					@include transition(background-color, color);
				}
			}

			&:hover {
				background-color: transparent;
				@include transition(background-color, color);
			}
		}

		&-info {
			p {
				font-size: 10px;
				font-weight: 600;
				line-height: 1;
				letter-spacing: 0.2px;
			}
		}

		&-payoff {
			p {
				font-size: 10px;
				font-weight: 600;
				line-height: 1;
				letter-spacing: 0.2px;
			}

			input {
				outline: none;
				width: 40px;
				height: 10px;
				border: none;
				box-shadow: none;
				border-radius: 0;
				border-bottom: 1px solid #333;
				font-size: 10px;
				font-weight: 600;
				line-height: 1;
				letter-spacing: 0.2px;
			}

			.dashboard__block-btn {
				height: 23px;
				padding-left: 10px;
				padding-right: 10px;
				font-size: 10px;
			}
		}

		&-strategy {
			p {
				font-size: 12px;
				font-weight: 500;
				letter-spacing: 0.08px;
			}

			input {
				outline: none;
				width: 175px;
				height: 40px;
				padding: 0 5px;
				box-shadow: none;
				border-radius: 6px;
				border: solid 1px rgba(185, 185, 185, 0.3);
			}
		}

		&-wrapper {}

		&-option {
			p {
				font-size: 14px;
				letter-spacing: 0.09px;
			}

			.dropdown {
				&--border {
					.dropdown-toggle {
						width: auto;
						min-width: unset;
						padding-left: 10px;
						padding-right: 10px;
					}
				}
			}
		}
	}
}

// SECTION dashboard :: END
